.wrapper {
  background-image: url("../../assets/rocksbg.png");

  background-size: contain;
  background-position: 0% 20%;
  background-color: #eef9f3;
  background-repeat: no-repeat;
}
.container {
  min-height: 110vw;
  display: flex;
  justify-content: center;

  .containerrr {
    width: 90%;
    height: 73vw;
    position: relative;
    .descriptionImage {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      z-index: 999;

      display: flex;
      flex-direction: column;
      align-items: center;

      span {
        background: maroon;
        border-radius: 1rem;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
          rgba(0, 0, 0, 0.22) 0px 10px 10px;
        color: aliceblue;
        padding: 0.3rem 1rem;
        width: fit-content;
        margin: 0 auto;

        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      }
      img {
        height: 50vw;
      }
    }
    .board {
      position: absolute;
      top: 55%;
      width: 100%;
    }
    .text {
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 85%;
      text-align: center;
      top: 80vw;
      font-size: 0.9rem;
      font-weight: 900;
    }
  }
}
.iframe {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  // width: 100%;
  // gap: 1rem;
  // padding: 1rem;

  gap: 1.5rem;
  margin: 1rem;
  padding: 1rem;
  // box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  // background-color: white;
  border-radius: 1rem;
  p {
    grid-column: 1/-1;
    text-align: center;
    font-size: 1.1rem;
    font-weight: 900;
  }
  iframe {
    display: block;
    margin: auto;
    width: 80%;
    aspect-ratio: 16/9;
    border: 0;
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
.des {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1rem;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 900;
}
.subscribe {
  padding: 1rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  button {
    width: fit-content;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
